import { Component, OnInit, Input, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR, NG_VALIDATORS } from '@angular/forms';
import { CurrencyService } from '../../shared/services/currency.service';
import { Animations } from '../../shared/animations/animations';


@Component({
  selector: 'app-currency-selector',
  templateUrl: './currency-selector.component.html',
  styleUrls: ['./currency-selector.component.scss'],
  providers: [
    { provide: NG_VALUE_ACCESSOR, useExisting: forwardRef(() => CurrencySelectorComponent), multi: true },
    { provide: NG_VALIDATORS, useExisting: forwardRef(() => CurrencySelectorComponent), multi: true }
  ]
})

export class CurrencySelectorComponent implements OnInit, ControlValueAccessor {
  @Input() currency: any;
  isSelectCurrency = false;
  selectedCurrency: any;
  icon_currency = '/assets/myicons/ml/currency.png';
  filteredItems: any;
  constructor(private currencyService: CurrencyService) {
    currencyService.options().subscribe(data => this.filteredItems = data);
  }

  ngOnInit() {

  }

  propagateChange: any = () => { };
  validateFn: any = () => { };

  displaySelectCurrency() {
    this.isSelectCurrency = true;
  }

  get counterValue() {
    return this.selectedCurrency;
  }

  set counterValue(val) {
    this.selectedCurrency = val;
    this.propagateChange(val);
  }

  ngOnChanges(inputs) {
    this.propagateChange(this.counterValue);
  }

  writeValue(value) {
    if (value) {
      this.selectedCurrency = value;
    }
  }

  registerOnChange(fn) {
    this.propagateChange = fn;
  }

  registerOnTouched() { }

  select(currency) {
    this.counterValue = currency;
    this.isSelectCurrency = false;
  }

  validate(c) {
    return this.validateFn(c);
  }
  filterItem(value) {
    if (!value) this.filteredItems = this.currencyService.options().subscribe(data => this.filteredItems = data); //when nothing has typed
    this.currencyService.options().subscribe(data =>  this.filteredItems =  data.filter(
      item => item.text.toLowerCase().indexOf(value.toLowerCase()) > -1
    ));

  }
}
